.main {
  background-image: url(../images/bg_graidents.svg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  height: 100vh;
  padding-top: 6rem;
}

.splash-form {
  background: #F9F9F9;
  max-width: 440px;
  margin-left: auto;
  margin-right: auto;
  text-align: center;

  .logo-part {
    background-color: #0799D4;
    padding: 50px 0;
    text-align: center;
  }

  .form-part {
    padding: 50px 30px 50px 30px;

    input {
      background: #fff;
      border: 1px solid #DBDBDB;
      text-align: center;
      padding: 0.7em;
      margin-bottom: 15px;
      width: 100%;
    }

    button {
      padding: 0.7em;
      border: 0;
      background-color: #0799D4;
      font-weight: 500;
      color: #fff;
      margin-bottom: 15px;
    }

    hr {
      background-color: #000000;
      margin-top: 5px;
      margin-bottom: 20px;
    }

    h3 {
      color: #2A2A2A;
      font-weight: 400;
      margin-bottom: 20px;
    }

    p {
      font-size: 13px;
      color: #333333;
      padding-right: 45px;
      padding-left: 45px;
      margin-bottom: 30px;
    }

    img {
      margin-top: 15px;
      margin-bottom: 55px;
    }

    .forget-link {
      font-weight: 500;
      color: #707070;
      font-size: 14px;

      a {
        color: #707070;
        text-decoration: underline;

        &:hover {
          text-decoration: none;
        }
      }
    }
  }
}

@media screen and (max-width: 768px) {
  body {
    padding-top: 0;
    height: auto;
  }
}