// Variables
$admin-header-height: 70px;
$admin-nav-width: 250px;
$admin-nav-mobile-width: 120px;

body {
  height: 100%;
  overflow-x: hidden;
}

.admin {
  --spacing: 1rem;

  display: flex;
  flex-wrap: wrap;

  display: grid;
  height: 100vh;

  grid-template-rows: $admin-header-height 1fr;
  grid-template-columns: $admin-nav-width 1fr;
  grid-template-areas: "header header"
  "nav    main";

  &_header {
    display: flex;
    flex-basis: 100%;
    grid-area: header;
    height: $admin-header-height;
    background-color: #fff;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
    position: relative;
  }

  &_nav {
    flex: 0 0 $admin-nav-width;
    grid-area: nav;
    background-color: #0799D4;
    color: #fff;
  }

  &_main {
    flex: 1;
    grid-area: main;
    padding: var(--spacing);
    overflow-y: auto;
    overflow-x: hidden;
    -webkit-overflow-scrolling: touch;

    background-color: #ECECEC;

    h2 {
      color: #005792;
      margin-bottom: 17px;
    }

    h4 {
      margin-bottom: 50px;
      color: #333333;

      img {
        margin-top: -5px;
      }

      a {
        color: #000000;
      }
    }

    .btn-primary {
      background-color: #0799D4 !important;

      img {
        margin-left: 7px;
        margin-top: -2px;
      }
    }

    .btn-secondary {
      background-color: #696969 !important;
    }
  }

  @media screen and (min-width: 48rem) {
    --spacing: 2rem;
  }
}

.dashboard {
  --column-count: 2;

  display: flex;
  flex-wrap: wrap;
  margin: 0 calc(var(--spacing) * -0.5);

  // Grid
  display: grid;
  grid-template-columns: repeat(var(--column-count), 1fr);
  grid-gap: var(--spacing);

  &_item {
    flex: 1 1 25%;
    grid-column-end: span 1;
    padding: calc(var(--spacing) / 2);

    &-full {
      flex-basis: 100%;
      grid-column: 1 / -1;
    }

    &-col {
      flex-basis: calc(100% / var(--column-count));
      grid-column-end: span 1;
    }
  }

  @media screen and (min-width: 48rem) {
    --column-count: 4;
  }

  @supports (display: grid) {
    margin: 0;

    &_item {
      padding: 0;
    }
  }
}

img {
  max-width: 100%;
  height: auto;
}

.logo {
  display: flex;
  flex: 0 0 $admin-nav-width;
  height: $admin-header-height;
  justify-content: center;
  align-items: center;
  position: relative;
  margin: 0;
  color: #fff;
  background-color: #005792;
  font-size: 1rem;

  h1 {
    margin: 0;
  }
}

.toolbar {
  display: flex;
  flex: 1;
  justify-content: flex-end;
  align-items: center;
  padding: 0 var(--spacing);
  background-color: #005792;
  color: #fff;

  > div {
    margin-bottom: 0;
    margin-left: 25px;
    font-weight: 500;

    &.avatar_image {
      img {
        margin-top: -6px;
      }
    }

    &.user_menu {
      margin-left: 10px;

      .username_text {
        text-decoration: underline;
        cursor: pointer;
        color: #fff;
      }
    }
  }

  .dropdown-menu {
    left: -60px;
    top: 57px;

    .dropdown-item {
      padding-bottom: 11px;
      padding-top: 11px;

      &.blue {
        color: #0799D4 !important;
      }
    }

    img {
      margin-top: -2px;
      margin-left: 3px;
    }
  }
}

.menu {
  list-style-type: none;
  padding: 60px 0 0;

  &_link {
    display: block;
    padding: 1rem 2rem;
    color: #fff;
    text-decoration: none;
    transition: all .2s ease-in-out;

    img {
      margin-top: -3px;
      margin-right: 11px;
    }

    &.active {
      background-color: #0975A0;
    }

    &:hover,
    &:focus {
      color: #fff;
      background-color: #0975A0;
      text-decoration: none;
    }
  }
}

.card {
  height: 100%;
  font-weight: 300;
  background-color: #fff;

  .content {
    padding: 17px 40px;

    table {
      margin-bottom: 45px;
      border: 1px solid #EAEAEA;

      thead tr th {
        padding-top: 23px;
        padding-bottom: 23px;
        vertical-align: middle;
        background-color: #EAEAEA;
        color: #333333;
      }

      td {
        vertical-align: middle;
        color: #333333;

        &.action-td {
          a {
            font-size: 28px;
            color: #333;

            &:hover {
              text-decoration: none;
            }
          }

          .dropdown-menu {
            right: 0;
            left: auto;
            top: 45px;

            a {
              font-size: 16px !important;
            }
          }
        }
      }

      .btn {
        padding: 5px 40px;
        font-size: 15px;
      }
    }

    .pagination {
      margin-bottom: 40px;
      justify-content: center;
      font-size: 14px;

      .page-item {
        .page-link {
          border: none;
          color: #333333;
          margin-right: 5px;
        }

        &.page-number .page-link {
          display: inline-block;
          width: 33px;
          height: 33px;
          border-radius: 100%;
        }

        &.active .page-link {
          background-color: #EAEAEA;
          color: #333333;
          font-weight: 500;
        }
      }
    }
  }

  &.stat {
    text-align: center;
    font-weight: 500;
    font-size: 18px;
    padding-top: 45px;
    padding-bottom: 45px;

    strong {
      display: block;
      color: #005792;
      font-size: 48px;
      font-weight: 600;
      border-bottom: 3px solid #005792;
      width: 70%;
      padding-bottom: 15px;
      margin: 0 auto 35px auto;
    }
  }
}


.main_part_header {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: stretch;
  align-content: space-between;
  padding: 20px 40px;
  margin-bottom: 30px;
  border-bottom: 1px solid #333333;

  .search-box {
    position: relative;
  }

  .search-input {
    color: #333333 !important;
    background-color: #E6E6E6;
    font-size: 16px;
    border: none;
    outline: none;
    padding: 9px;
    border-radius: 7px;
  }

  .search-icon {
    position: absolute;
    top: 14px;
    right: 14px;
    cursor: pointer;
  }
}

.btn {
  display: inline-block;
  border-radius: 0.4em;
  border: 0;
  padding: 0.5rem 1rem;
  white-space: nowrap;
}

@media screen and (max-width: 768px) {
  .admin {
    grid-template-columns: $admin-nav-mobile-width 1fr !important;
    grid-template-areas: "header header"
    "main    main";

    &_nav {
      display: none;
    }
  }

  .admin_nav {
    flex: 0 0 $admin-nav-mobile-width;
  }

  .logo {
    padding-left: 10px;
    flex: 0 0 $admin-nav-mobile-width;
  }

  .dashboard {
    &_item {
      flex-basis: 100%;
      grid-column: 1 / -1;
    }
  }

  .card .content {
    display: block;
  }
}