.dashboard {
  --column-count: 5;

  &_item {
    align-self: start;

    &-md-4 {
      flex: 1 2 100%;
      grid-column-end: span 2;
    }
  }

  @media screen and (min-width: 48rem) {
    --column-count: 5;
  }
}

.admin_main {
  h4 {
    margin-bottom: 0;
  }
}

.org_info {
  padding: 40px 25px;

  .org_image {
    background-color: #005792;
    width: 140px;
    height: 140px;
    display: inline-block;
    text-align: center;
    line-height: 140px;
    float: left;
    margin-right: 20px;
    margin-bottom: 30px;
  }

  h3 {
    display: inline-block;
    color: #696969;

    &.org_name {
      color: #005792;
      margin-top: 23px;
    }
  }

  table {
    clear: both;
    width: 100%;
    color: #5D5D5D;
    font-weight: 400;

    td {
      width: 50%;
    }
  }
}

