.dashboard {
  .main_part_header {
    h2 {
      span {
        color: #696969;
        font-size: 16px;
        font-weight: 400;
      }
    }
  }

  .content {
    padding-bottom: 37px;

    .setting-col {
      width: 25%;
      display: inline-block;
      vertical-align: top;

      .block {
        margin-bottom: 27px;

        p {
          margin-bottom: 3px !important;
        }

        h3 {
          margin-top: 3px;
          color: #005792;
        }
      }
    }

    &.terms-settings {
      h2 {
        color: #333333;
      }

      p {
        color: #333333;
        font-weight: 400;
        margin-bottom: 15px;
      }

      .edit-link {
        margin-top: 25px;
        margin-bottom: 30px;
      }
    }
  }
}