.dashboard {
  --column-count: 4;

  &_item {
    align-self: start;

    &-md-3 {
      flex: 1 1 100%;
      grid-column-end: span 1;
    }

    &-md-9 {
      flex: 1 3 100%;
      grid-column-end: span 3;
    }
  }

  @media screen and (min-width: 48rem) {
    --column-count: 4;
  }
}

.admin_main {
  h4 {
    margin-bottom: 0;
  }
}

.info_card {
  margin-bottom: 25px;
  padding: 35px 20px;

  table {
    clear: both;
    width: 100%;
    color: #5D5D5D;
    font-weight: 400;

    td {
      width: 50%;
    }
  }

  p {
    color: #707070;
    font-weight: 400;
    margin-bottom: 30px;
  }

  h4 {
    color: #0A0A0A;
    font-size: 14px;
    margin-bottom: 15px;
  }

  h3 {
    color: #000000;
    padding-bottom: 10px;
    margin-bottom: 35px;
    border-bottom: 1px solid #707070;
    display: inline-block;
    width: 150px;
    max-width: 100%;
  }

  a {
    color: #2699FB;
    text-decoration: underline;
    font-weight: 500;
    font-size: 15px;
    margin-top: 20px;
    display: inline-block;
  }
}

.result_card {
  margin-bottom: 25px;

  h3 {
    color: #000000;
    margin-bottom: 35px;
    padding: 10px;
    border-bottom: 1px solid #DBDBDB;
  }

  table {
    margin-bottom: 30px;

    th {
      color: #000000 !important;
      background-color: #F2F3FA;
    }

    td {
      color: #313030 !important;
      vertical-align: middle;
    }

    tr {
      border-top: 0;
    }
  }
}

.admin_info {
  padding: 40px 25px;

  p {
    color: #696969;
    font-weight: 400;
  }

  table {
    clear: both;
    width: 100%;
    color: #5D5D5D;
    font-weight: 400;

    td {
      width: 50%;
    }
  }
}