body {
  height: 100%;
  overflow-x: hidden; }

.admin {
  --spacing: 1rem;
  display: flex;
  flex-wrap: wrap;
  display: grid;
  height: 100vh;
  grid-template-rows: 70px 1fr;
  grid-template-columns: 250px 1fr;
  grid-template-areas: "header header" "nav    main"; }
  .admin_header {
    display: flex;
    flex-basis: 100%;
    grid-area: header;
    height: 70px;
    background-color: #fff;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
    position: relative; }
  .admin_nav {
    flex: 0 0 250px;
    grid-area: nav;
    background-color: #0799D4;
    color: #fff; }
  .admin_main {
    flex: 1;
    grid-area: main;
    padding: var(--spacing);
    overflow-y: auto;
    overflow-x: hidden;
    -webkit-overflow-scrolling: touch;
    background-color: #ECECEC; }
    .admin_main h2 {
      color: #005792;
      margin-bottom: 17px; }
    .admin_main h4 {
      margin-bottom: 50px;
      color: #333333; }
      .admin_main h4 img {
        margin-top: -5px; }
      .admin_main h4 a {
        color: #000000; }
    .admin_main .btn-primary {
      background-color: #0799D4 !important; }
      .admin_main .btn-primary img {
        margin-left: 7px;
        margin-top: -2px; }
    .admin_main .btn-secondary {
      background-color: #696969 !important; }
  @media screen and (min-width: 48rem) {
    .admin {
      --spacing: 2rem; } }

.dashboard {
  --column-count: 2;
  display: flex;
  flex-wrap: wrap;
  margin: 0 calc(var(--spacing) * -0.5);
  display: grid;
  grid-template-columns: repeat(var(--column-count), 1fr);
  grid-gap: var(--spacing); }
  .dashboard_item {
    flex: 1 1 25%;
    grid-column-end: span 1;
    padding: calc(var(--spacing) / 2); }
    .dashboard_item-full {
      flex-basis: 100%;
      grid-column: 1 / -1; }
    .dashboard_item-col {
      flex-basis: calc(100% / var(--column-count));
      grid-column-end: span 1; }
  @media screen and (min-width: 48rem) {
    .dashboard {
      --column-count: 4; } }
  @supports (display: grid) {
    .dashboard {
      margin: 0; }
      .dashboard_item {
        padding: 0; } }

img {
  max-width: 100%;
  height: auto; }

.logo {
  display: flex;
  flex: 0 0 250px;
  height: 70px;
  justify-content: center;
  align-items: center;
  position: relative;
  margin: 0;
  color: #fff;
  background-color: #005792;
  font-size: 1rem; }
  .logo h1 {
    margin: 0; }

.toolbar {
  display: flex;
  flex: 1;
  justify-content: flex-end;
  align-items: center;
  padding: 0 var(--spacing);
  background-color: #005792;
  color: #fff; }
  .toolbar > div {
    margin-bottom: 0;
    margin-left: 25px;
    font-weight: 500; }
    .toolbar > div.avatar_image img {
      margin-top: -6px; }
    .toolbar > div.user_menu {
      margin-left: 10px; }
      .toolbar > div.user_menu .username_text {
        text-decoration: underline;
        cursor: pointer;
        color: #fff; }
  .toolbar .dropdown-menu {
    left: -60px;
    top: 57px; }
    .toolbar .dropdown-menu .dropdown-item {
      padding-bottom: 11px;
      padding-top: 11px; }
      .toolbar .dropdown-menu .dropdown-item.blue {
        color: #0799D4 !important; }
    .toolbar .dropdown-menu img {
      margin-top: -2px;
      margin-left: 3px; }

.menu {
  list-style-type: none;
  padding: 60px 0 0; }
  .menu_link {
    display: block;
    padding: 1rem 2rem;
    color: #fff;
    text-decoration: none;
    transition: all .2s ease-in-out; }
    .menu_link img {
      margin-top: -3px;
      margin-right: 11px; }
    .menu_link.active {
      background-color: #0975A0; }
    .menu_link:hover, .menu_link:focus {
      color: #fff;
      background-color: #0975A0;
      text-decoration: none; }

.card {
  height: 100%;
  font-weight: 300;
  background-color: #fff; }
  .card .content {
    padding: 17px 40px; }
    .card .content table {
      margin-bottom: 45px;
      border: 1px solid #EAEAEA; }
      .card .content table thead tr th {
        padding-top: 23px;
        padding-bottom: 23px;
        vertical-align: middle;
        background-color: #EAEAEA;
        color: #333333; }
      .card .content table td {
        vertical-align: middle;
        color: #333333; }
        .card .content table td.action-td a {
          font-size: 28px;
          color: #333; }
          .card .content table td.action-td a:hover {
            text-decoration: none; }
        .card .content table td.action-td .dropdown-menu {
          right: 0;
          left: auto;
          top: 45px; }
          .card .content table td.action-td .dropdown-menu a {
            font-size: 16px !important; }
      .card .content table .btn {
        padding: 5px 40px;
        font-size: 15px; }
    .card .content .pagination {
      margin-bottom: 40px;
      justify-content: center;
      font-size: 14px; }
      .card .content .pagination .page-item .page-link {
        border: none;
        color: #333333;
        margin-right: 5px; }
      .card .content .pagination .page-item.page-number .page-link {
        display: inline-block;
        width: 33px;
        height: 33px;
        border-radius: 100%; }
      .card .content .pagination .page-item.active .page-link {
        background-color: #EAEAEA;
        color: #333333;
        font-weight: 500; }
  .card.stat {
    text-align: center;
    font-weight: 500;
    font-size: 18px;
    padding-top: 45px;
    padding-bottom: 45px; }
    .card.stat strong {
      display: block;
      color: #005792;
      font-size: 48px;
      font-weight: 600;
      border-bottom: 3px solid #005792;
      width: 70%;
      padding-bottom: 15px;
      margin: 0 auto 35px auto; }

.main_part_header {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: stretch;
  align-content: space-between;
  padding: 20px 40px;
  margin-bottom: 30px;
  border-bottom: 1px solid #333333; }
  .main_part_header .search-box {
    position: relative; }
  .main_part_header .search-input {
    color: #333333 !important;
    background-color: #E6E6E6;
    font-size: 16px;
    border: none;
    outline: none;
    padding: 9px;
    border-radius: 7px; }
  .main_part_header .search-icon {
    position: absolute;
    top: 14px;
    right: 14px;
    cursor: pointer; }

.btn {
  display: inline-block;
  border-radius: 0.4em;
  border: 0;
  padding: 0.5rem 1rem;
  white-space: nowrap; }

@media screen and (max-width: 768px) {
  .admin {
    grid-template-columns: 120px 1fr !important;
    grid-template-areas: "header header" "main    main"; }
    .admin_nav {
      display: none; }
  .admin_nav {
    flex: 0 0 120px; }
  .logo {
    padding-left: 10px;
    flex: 0 0 120px; }
  .dashboard_item {
    flex-basis: 100%;
    grid-column: 1 / -1; }
  .card .content {
    display: block; } }
